@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');
@import "header";
@import "introduce";
@import "metaverse";
@import "features";
@import "for-what";
@import "app";
@import "profit";
@import "dashboard";
@import "conference_hall";
@import "faq";



* {
  box-sizing: border-box;
}

body {
  background: #272552;
  font-family: Montserrat, sans-serif;
  padding: 0;
  color: #fff;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.main-screen {
  background-image: url("../images/first-background.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  min-height: 100vh;

  &__space {
    background: #15133d;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0%) 0%, #15133d 100%);
    width: 100%;
    height: 120px;
  }
}

.main-buildings-text{
  position: absolute;
  z-index: 6;
  transform: rotate(-90deg);
  margin-bottom: 60em;
  margin-left: -23vw;


  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 275;
  font-size: 66px;
  line-height: 80px;
  letter-spacing: 0.48em;

  color: #FFFFFF;

  opacity: 0.2;
}

.colliseum{
  margin-top: -5%;
  height: 1200px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url(../images/colliseum_back.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  box-shadow: 0 0 200px 250px #181541;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  z-index: 5;
  h1{
    width: 50%;
    font-style: italic;
    font-weight: 300;
    font-size: 26px;
    line-height: 35px;
    text-align: center;
    &.mobile{
      display: none;
    }
  }
  div{
    width: 30%;
    margin: 10% 15%;
    align-self: flex-start;
    h1{
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 900;
      font-size: 32px;
      line-height: 39px;
      width: 100%;
      text-align: left;
    }
    p{
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      width: 89%;
    }
  }
  img{
    display: none;
  }
}

.hallverfull{
  position: relative;
  display: flex;
  flex-direction: column;
  background-image: url(../images/hallverfull_back.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  z-index: 6;
}

.footer {
  padding-top: 30px;
  padding-bottom: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  box-shadow: 0 0 100px 100px #272552;
  .logo{
    margin-top: 148px;
  }
}

.icons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35px;
}

.copyright {
  font-size: 16px;
  line-height: 20px;
  margin-top: 15px;
}

.icon {
  margin: 0 10px;

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__image {
    width: 32px;
  }
}

.copyright__mobile {
  display: none;
}

.popup {
  overflow: hidden;
}

.mobile-menu {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #201D4F;
  z-index: 999;
  top: 0;
  display: flex;
  flex-direction: column;
}

.ui-widget-overlay {
  background: #000 !important;
  opacity: 0.6 !important;
}

.ui-dialog-titlebar {
  padding: 0 !important;
  border: none !important;
}

.ui-dialog-title {
  display: none !important;
}

[role="dialog"] {
  padding: 0 !important;
}

video {
  width: 100%;
  height: 100%;
}

.ui-button {
  z-index: 99 !important;
  top: 0 !important;
  right: -10px !important;
}

#dialog {
  padding: 0 !important;
}

.dialogs {
  display: none;
}

.menu-button {
  padding: 30px;

  &__image {
    max-width: 15px;
  }
}

.menu-list {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.menu-item {
  font-weight: 600;
  font-size: 22px;
  line-height: 120%;
  text-decoration: none;
  margin-top: 40px;
  color: #fff;

  &:first-child {
    margin-top: 50px;
  }

  &:last-child {
    margin-bottom: 70px;
  }
}

.menu-footer {
  justify-self: baseline;
  align-self: center;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  padding-bottom: 70px;
}


// roadmap


.roadmap {
  overflow: hidden;
  background-image: url("../images/roadmap.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 100px;

  &__container {
    max-width: 1200px;
    margin: 0 auto;
  }

  &__title {
    font-weight: 900;
    font-size: 32px;
    line-height: 39px;
    padding-top: 45px;
    padding-left: 85px;
  }
}

.map-item__step--pipe-first-active,
.map-item__step--pipe-second-active,
.map-item__step--active {
  opacity: 0;
}

.map {
  margin-top: 50px;
  position: relative;

  &-item {
    position: absolute;

    &--active {
      & .map-item__step--pipe-first-active,
      & .map-item__step--pipe-second-active,
      & .map-item__step--active {
        opacity: 1;

      }
    }

    &--active:nth-child(5) {
      & .map-item__step--pipe-first-active,
      & .map-item__step--pipe-second-active,
      & .map-item__step--active {
        opacity: 1 !important;

      }
    }

    &--current {
      & .map-item__step--pipe-first-active,
      & .map-item__step--pipe-second-active
      {
        opacity: 0;

      }
    }

    &--current {
      & .map-item__step--man {
        opacity: 1;

      }
    }

    &__step {
      position: absolute;
      top: 0;
      left: 0;

      transition: 1s all;

      &--man {
        top: -62px;
        left: 60px;
        opacity: 0;
      }

      &--button {
        width: 200px;
        height: 120px;
        //background: #000;
        top: 195px;
        left: 100px;
        opacity: 0.5;
        cursor: pointer;
        z-index: 999;
      }
    }

    &--skeleton {
      opacity: 0.2;
    }

    &[data-id="1"] {
      top: 110px;
      left: -58px;

      & .map-item__step--pipe-first {
        top: 50px;
        left: 58px;
      }

      & .map-item__step--pipe-first-active {
        top: 50px;
        left: 58px;
      }

      & .map-item__step--pipe-second {
        left: 207px;
        top: 42px;
      }

      & .map-item__step--pipe-second-active {
        left: 207px;
        top: 42px;
      }
    }

    &[data-id="2"] {
      top: 71px;
      left: 173px;

      & .map-item__step--pipe-first {
        top: 81px;
        left: 210px;
      }

      & .map-item__step--pipe-first-active {
        top: 81px;
        left: 210px;
      }
    }

    &[data-id="3"] {
      top: 101px;
      left: 402px;

      & .map-item__step--pipe-first {
        top: 50px;
        left: 218px;
      }

      & .map-item__step--pipe-first-active {
        top: 50px;
        left: 218px;
      }
    }

    &[data-id="4"] {
      top: 65px;
      left: 634px;

      & .map-item__step--pipe-first {
        top: 92px;
        left: 212px;
      }

      & .map-item__step--pipe-first-active {
        top: 92px;
        left: 212px;
      }
    }

    &[data-id="5"] {
      top: 95px;
      left: 859px;

      & .map-item__step--pipe-first {
        top: 57px;
        left: 243px;
      }

      & .map-item__step--pipe-first-active {
        top: 57px;
        left: 243px;
      }
    }
  }
}

.menu-button {
  display: none;
}

.feature-card {
  display: flex;
  max-width: 540px;
  background: #353272;
  border-radius: 30px;
  margin-top: 30px;

  &__image {
    border-radius: 30px;
  }

  &__description {
    padding: 30px;
    display: flex;
    flex-direction: column;
  }

  &__level {
    font-size: 14px;
    line-height: 17px;
  }

  &__title {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    margin-top: 5px;

    color: #E999C7;
  }

  &__text {
    margin-top: 15px;
    font-size: 14px;
    line-height: 17px;

    color: #837ED2;
  }

  &__button {
    &-text {
      font-weight: bold;
      font-size: 20px;
      line-height: 24px;
      color: #FF67A2;
      margin-left: 15px;
    }

    &-container {
      margin-top: 30px;
      display: flex;
      align-items: center;
      cursor: pointer;
    }

    &-image {
      width: 100%;
    }

    &-image-container {
      max-width: 30px;
    }
  }
}

.roadmap-description {
  display: flex;
  margin-top: 575px;
  min-height: 340px;
  padding-left: 85px;

  &__image {
    display: none;
    &--step{

    }
  }

  &__container {
    display: none;
    width: 100%;
    flex-direction: column;

    &--active {
      display: flex;
    }
  }

  &__row-container {
    display: flex;
  }

  &__row {
    flex: 1;
    padding-right: 80px;
  }

  &__title {
    font-weight: 800;
    font-size: 28px;
    line-height: 34px;
    margin-bottom: 15px;
  }

  &__subtitle {
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
  }

  &__text {
    font-size: 16px;
    line-height: 20px;
    margin-top: 10px;
  }
}

.slider-button {
  position: absolute;
  z-index: 999;
  width: 5.2%;
  margin-left: -2%;

  &__image {
    max-width: 34px;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    float: left;
    cursor: pointer;
  }

  &__text {
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    color: #1E1C4C;
    opacity: 0.5;
    margin-top: 10px;
  }
}

.for-what-image {
  margin-bottom: -7px;
}

.roadmap-description__container[data-id="0"]{
  display: none;
}

.mobile-roadmap-description{
  display: none;
}


